<template>
	<view>
		<view class="tabbar_after_box">
			<view class="after"></view>
		</view>
		<view class="tabbar_box">
			<view class="tabbar">
				<view class="label" v-for="(item, index) in tabbarList" :key="index" @click.stop="selectTabbarTap(index)">
					<view class="icons">
						<aicon :type="current===index?item.icon_active:item.icon_smiles" :color="current===index?color_active:color_smiles" size="30"></aicon>
					</view>
					<view class="name" :style="{color: current===index?color_active:color_smiles}">{{item.name}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'tabbar',
		props: {
			current: {
				type: Number,
				default: 0
			}
			
		},
		data() {
			return {
				color_active: '#007AFF',
				color_smiles: '#8A8B9C',
				tabbarList: [{
					name: '首页',
					icon_active: 'home-smile-2-fill',
					icon_smiles: 'home-smile-2-line',
					path: '/pages/index/index'
				}, {
					name: '资讯',
					icon_active: 'map-2-fill',
					icon_smiles: 'map-2-line',
					path: '/pages/article/index'
				}, {
					name: '下单',
					icon_active: 'map-2-fill',
					icon_smiles: 'map-2-line',
					path: '/pages/order/index'
				}, {
					name: '商城',
					icon_active: 'map-2-fill',
					icon_smiles: 'map-2-line',
					path: '/pages/shop/index'
				}, {
					name: '我的',
					icon_active: 'bear-smile-fill',
					icon_smiles: 'bear-smile-line',
					path: '/pages/user/index'
				}]
			}
		},
		methods: {
			selectTabbarTap(index){
				let self = this;
				let path = self.tabbarList[index].path;
				uni.switchTab({url: path});
			}
		}
	}
</script>

<style lang="less">
	.tabbar_after_box{
		width: 100%;
		padding-bottom: env(safe-area-inset-bottom);
		.after{
			width: 100%;
			height: 120rpx;
		}
	}
	.tabbar_box{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		z-index: 1000;
		background-color: #fff;
		box-shadow: 0 4rpx 10rpx #00000040;
		padding-bottom: env(safe-area-inset-bottom);
		.tabbar{
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 120rpx;
			width: 100%;
			.label{
				flex: 1;
				min-width: 0;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				.icons{
					width: 50rpx;
					height: 50rpx;
					display: flex;
					align-items: center;
					justify-content: center;
				}
				.name{
					font-size: 28rpx;
				}
			}
		}
	}
</style>
